import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  userInfo: [
    {
      name: "13212345678",
      pass: "132456"
    }
  ],
  loginUser: "",
  cartInfo: [],
  payCartInfo: [],
  addressInfo: [
    {
      id: 1001,
      name: "",
      tel: "18600639094",
      province: "",
      city: "",
      county: "",
      detailed: "",
      address: "",
      isDefault: true
    },
    {
      id: 1002,
      name: "",
      tel: "13212345678",
      province: "",
      city: "",
      county: "",
      detailed: "7501",
      address: "7501",
      isDefault: false
    },
    {
      id: 1003,
      name: "",
      tel: "15868863541",
      province: "",
      city: "",
      county: "",
      detailed: "SOHO5512",
      address: "SOHO5512",
      isDefault: false
    }
  ],
  checkedAddress: {
    id: 1001,
    name: "",
    tel: "18600639094",
    province: "",
    city: "",
    county: "",
    detailed: "",
    address: "",
    isDefault: true
  }
}

const mutations = {
  addUserInfo(state, val) {
    state.userInfo.push(val)
  },
  setLoginUser(state, val) {
    state.loginUser = val
  },
  addToCart(state, val, type) {
    const { cartInfo } = state;
    let productIndex = 0;
    const flag = cartInfo.some((item, index) => {
      productIndex = index;
      return item.id == val.id;
    })
    if (flag) {
      state.cartInfo[productIndex].num++;
    } else {
      state.cartInfo.push(val)
    }
    localStorage.setItem('cartInfo', JSON.stringify(state.cartInfo))
  },
  setCartInfo(state) {
    state.cartInfo = JSON.parse(localStorage.getItem('cartInfo')) || []
  },
  setPayCartInfo(state, val) {
    state.payCartInfo = val;
    localStorage.setItem('payCartInfo', JSON.stringify(val))
  },
  setCheckedAddress(state, val) {
    state.checkedAddress = val;
    localStorage.setItem('checkedAddress', val);
  },
  addAddressInfo(state, val) {
    let idArr = [], newId = 0;
    state.addressInfo.forEach(item => {
      idArr.push(item.id)
    });
    newId = Math.max(...idArr)
    val.id = newId + 1;
    if (val.isDefault) {
      state.addressInfo.forEach(item => {
        item.isDefault = false
      });
    }
    state.addressInfo.push(val);
    localStorage.setItem("addressInfo", JSON.stringify(state.addressInfo))
  }
}

export default new Vuex.Store({
  state,
  mutations
})